@import url(https://fonts.googleapis.com/css?family=Poppins:400,700&display=swap);
body {
    margin: 0;
    font-family: Arial, sans-serif;
}

h1, h2, h3 {
    color: #333;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

.button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
}

.button:hover {
    background-color: #0056b3;
}

.card {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 15px;
    margin: 10px 0;
}

.card-title {
    font-size: 1.5em;
    margin-bottom: 10px;
}

.card-content {
    font-size: 1em;
    color: #555;
}
body {
  font-family: "Poppins", sans-serif;
  background: linear-gradient(135deg, #1a2a6c, #b21f1f, #fdbb2d);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  flex-direction: column;
}

.login-container {
  background: rgba(255, 255, 255, 0.1);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  width: 100%;
  max-width: 380px;
  text-align: center;
  animation: fadeIn 0.5s ease-in-out;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

@keyframes fadeIn {
  0% { opacity: 0; transform: translateY(-10px); }
  100% { opacity: 1; transform: translateY(0); }
}

h2 {
  color: white;
  font-size: 24px;
  margin-bottom: 15px;
}

input[type="email"], input[type="password"] {
  width: 100%;
  padding: 12px;
  margin: 12px 0;
  border: none;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  outline: none;
  transition: 0.3s;
  font-size: 16px;
  text-align: center;
}

input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

input:focus {
  border: 2px solid #fdbb2d;
  box-shadow: 0 0 10px #fdbb2d;
}

button {
  width: 100%;
  padding: 12px;
  background: #fdbb2d;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  transition: 0.3s;
  font-weight: bold;
}

button:hover {
  background: #ffcc70;
  transform: scale(1.05);
}

.toggle-link, .reset-link {
  color: white;
  font-size: 14px;
  margin-top: 12px;
  cursor: pointer;
  transition: 0.3s;
}

.toggle-link:hover, .reset-link:hover {
  text-decoration: underline;
  color: #ffcc70;
}

.error-message {
  color: #ff4d4d;
  font-size: 14px;
  margin-top: 8px;
}

footer {
  text-align: center;
  color: white;
  margin-top: 20px;
  font-size: 14px;
}

@media (max-width: 600px) {
  .login-container {
    padding: 25px;
  }

  h2 {
    font-size: 22px;
  }

  input, button {
    font-size: 14px;
    padding: 10px;
  }
}
